	module.exports = {"data":{"post":{"html":"<p>It's Valentine's Day. Mama and I just remembered... we first noticed each another 20 years ago today! (Actually, it was the first or second weekend in February 1980, which would be the 3rd or the 10th. But Valentine's Day is easier to remember.) It was a gathering of <a href=\"http://www.afs.org\">AFS</a> exchange students. I had just returned from Brazil, she was spending a year in Minnesota.</p>\n<p>Wired: <a href=\"http://www.wired.com/news/business/0,1367,34308,00.html\"><br />\nNet Liberation Day in Germany</a> \"'More use means more e-commerce'... And Schröder believes more e-commerce will mean more jobs.\" </p>\n<p>Thanks to the lack of material and baby pictures lately, I'm starting to fall in the ETP <a href=\"http://www.editthispage.com/Top100\">Top 100</a> <i>It's about time!</i></p>\n<p>On the other hand, <a href=\"http://Mama.editthispage.com\">Mama</a> is starting to gain self-confidence. She mailed me at work:<br />\n\"Hey, I'm starting to get good at this....\" <i>It's the magic of being able to Edit This Page!</i></p>\n<p>Suggestion to Userland: How about reserving the domain 'edithispage.com' (with only one t). That's how I always mistype it. \";->\" <i>Edit His Page? Isn't that sexist?</i></p>\n<p><a href=\"http://spicynoodles.com\">Spicy Noodles</a>: \"<a href=\"http://www.fool.com\">The Motley Fool</a> opens <a href=\"http://www.fool.de\">Motley Fool Germany</a>. <i>Cool!</i>\" Super cool!</p>","fields":{"title":"Edit His Page","date":"14 Feb 2000","url":"https://papascott.de/2000-02-14-edit-his-page/"}}},"pathContext":{"slug":"/2000-02-14-edit-his-page/","prev":{"fields":{"title":"Sunday","date":"13 Feb 2000","slug":"/2000-02-13-sunday/"}},"next":{"fields":{"title":"","date":"15 Feb 2000","slug":"/2000-02-15-jokes-on-yoe/"}}}}

